import {
  getMonthInt,
  getMonthNameByNumber
} from 'orquestra-helpers'

export const fillMonthsOnPeriod = ({
  start,
  end,
  data,
  defaultParams
}) => {
  const monthlyEntries = []
  const currDate = new Date(start.getTime())

  // eslint-disable-next-line no-unmodified-loop-condition
  while (currDate <= end) {
    const currYear = currDate.getFullYear()
    const currMonth = getMonthInt(currDate)

    const monthlyData = data.find(({ month, year }) => {
      return month === currMonth && year === currYear
    })

    if (monthlyData) {
      monthlyEntries.push(monthlyData)
    } else {
      monthlyEntries.push({
        month: currMonth,
        monthName: getMonthNameByNumber(currMonth),
        year: currYear,
        ...defaultParams
      })
    }

    currDate.setMonth(currDate.getMonth() + 1)
  }

  return monthlyEntries
}
