import { TableMultivalue } from '../../components/table-multivalue'
import TextareaAutosize from '../../components/textarea-autosize'
import ButtonRadio from '../../components/button-radio'
import DatepickerRange from '../../components/datepicker-range'

const TableRecommendations = function () {
  const tableSelector = '#tbl-recommendations'
  const fieldDecriptionId = 'recomendacoesDescricao'

  const $refs = {
    section: document.querySelector('#section-recommendations'),
    table: document.querySelector(tableSelector),
    executedLabels: document.querySelectorAll('[xid=divplanoExecutado]')
  }

  const state = {
    alias: null
  }

  /**
   * Public Methods
   */
  function mount ({ alias, hasToResetTable }) {
    const isReadonly = ['elaboracaoAnual', 'complementacaoAnual'].includes(alias)

    state.alias = alias

    if (isReadonly) {
      return setTableReadonly()
    }

    const Table = new TableMultivalue(tableSelector, onRowMount)

    if (hasToResetTable) {
      Table.reset()
    }

    return Table
  }

  function hasToMount ({ alias, productType }) {
    return [
      'complementacao',
      'elaboracaoAnual',
      'complementacaoAnual'
    ].includes(alias) ||
    (productType === 'PPRA' && alias === 'elaboracao')
  }

  function show () {
    $refs.section
      .classList
      .remove('hidden')
  }

  function destroy () {
    $refs.section.remove()
  }

  /**
   * Private Methods
   */
  function onRowMount (row) {
    ButtonRadio
      .init('.btn-radio-wrapper', true, row)

    TextareaAutosize(
      row.querySelector(`[xname=inp${fieldDecriptionId}]`)
    )

    DatepickerRange
      .init('.input-monthrange', {
        startView: 'months',
        minViewMode: 'months',
        format: 'M/yyyy'
      }, row)
  }

  function setTableReadonly () {
    $refs.table
      .classList
      .add('-readonly', '-hover')

    DatepickerRange
      .init(`${tableSelector} .input-monthrange`, {})
  }

  return {
    mount,
    hasToMount,
    show,
    destroy
  }
}

export default TableRecommendations()
