import {
  getMonthInt,
  getMonthNameByNumber
} from 'orquestra-helpers'

import { fillMonthsOnPeriod } from './util'

const ORQUESTRA_DATASOURCE_URL = process.env.DATASOURCE_BASE_URL + process.env.DATASOURCE_COORDINATION

const config = {
  defaultParams: {
    quantity: 0
  }
}

const getDataFromDataSource = function ({
  start,
  end,
  cnpj
}) {
  const url = new URL(ORQUESTRA_DATASOURCE_URL)

  const params = {
    inpperiodoInicial: getMonthInt(start),
    inpanoInicial: start.getFullYear(),
    inpperiodoFinal: getMonthInt(end),
    inpanoFinal: end.getFullYear(),
    inpcnpj: cnpj.replace(/\D/g, '')
  }

  url.search = new URLSearchParams(params).toString()

  return fetch(url)
    .then(res => res.json())
    .then(({ success }) => {
      const data = parseData(success)

      return {
        data,
        start,
        end
      }
    })
}

const parseData = success => {
  return success.map(entry => {
    return {
      month: parseInt(entry.txt),
      monthName: getMonthNameByNumber(entry.txt),
      year: parseInt(entry.fields.ano),
      quantity: parseInt(entry.fields.quantidade)
    }
  })
}

function getDataFromPeriod (params) {
  return new Promise((resolve, reject) => {
    getDataFromDataSource(params)
      .then(({ start, end, data }) => {
        if (!data.length) {
          return resolve(data)
        }

        const { defaultParams } = config
        const filledData = fillMonthsOnPeriod({
          start,
          end,
          data,
          defaultParams
        })
        return resolve(filledData)
      })
      .catch(err => reject(err))
  })
}

function formatData (data) {
  return data.map(entry => {
    const rowData = {}
    rowData.coordenacaoMesAno = `${entry.monthName.substring(0, 3)}/${entry.year}`
    rowData.coordenacaoRegistros = entry.quantity || '-'

    return rowData
  })
}

export default {
  getDataFromPeriod,
  formatData
}
