function mount (chips, config) {
  const {
    inputPlaceholder,
    errorMessage
  } = config

  const inputHtml = createInput(inputPlaceholder, errorMessage)
  chips.insertAdjacentHTML('beforeend', inputHtml)

  if (config.readonly) {
    config.editable = false
    config.removeable = false

    setReadonly(chips)
  } else {
    setTriggers(chips, config)
  }

  if (config.target.value !== '') {
    const values = config.target.value
    config.target.value = ''

    return values
      .split(';')
      .forEach(value => addChip(chips, value, config))
  }

  if (config.data) {
    return config.data
      .forEach(value => addChip(chips, value, config))
  }
}

function setReadonly (chips) {
  const input = getChipsInput(chips)

  chips.classList.add('-readonly')
  input.style.display = 'none'
}

function setTriggers (chips, config) {
  const input = getChipsInput(chips)

  chips.addEventListener('click', function () {
    focus(this)
  })

  input.addEventListener('focus', handleInputFocus)
  input.addEventListener('blur', handleInputBlur)

  input.addEventListener('change', function () {
    handleInputChange(this, config)
  })

  input.addEventListener('keydown', function (e) {
    if (e.key === 'Enter') {
      e.preventDefault()
      handleInputChange(this, config)
    }
  })

}

function mountSingleChip (chips, value, config) {
  const inputWrapper = chips.querySelector('.chip-input-wrapper')
  const sanitized = sanitizeValue(value)
  const chipHtml = createChip(sanitized, config)
  const eventChange = new Event('change')

  inputWrapper.insertAdjacentHTML('beforebegin', chipHtml)

  const chipsValues = config.target.value
  const chip = [...chips.querySelectorAll('.chip')].pop()
  const removeBtn = chip.querySelector('.chip-action[data-chip-action="remove"]')
  const editBtn = chip.querySelector('.chip-action[data-chip-action="edit"]')

  config.target.value = chipsValues === ''
    ? sanitized
    : `${chipsValues};${sanitized}`

  config.target.dispatchEvent(eventChange)

  if (removeBtn) {
    removeBtn.addEventListener('click', function () {
      handleRemoveChip.call(this, config.target)
    })
  }

  if (editBtn) {
    chip.addEventListener('dblclick', function () {
      handleEditChip.call(editBtn, config)
    })
    editBtn.addEventListener('click', function () {
      handleEditChip.call(this, config)
    })
  }
}

function mountEditChip (chip, config) {
  const chips = chip.closest('.chips')
  const index = getChipIndex(chip)
  const values = config.target.value.split(';')
  const value = values[index]
  const inputHtml = createInput('', config.errorMessage, value)

  config = { ...config, chip, value, index }
  chip.insertAdjacentHTML('afterend', inputHtml)
  chip.classList.add('hidden')

  disabledAdd(chips)

  const editInput = chips.querySelector('.chip-input')
  editInput.focus()
  editInput.setSelectionRange(0, editInput.value.length)

  editInput.addEventListener('focus', handleInputFocus)

  editInput.addEventListener('blur', function () {
    handleEditChange(this, config)
  })

  editInput.addEventListener('keydown', function (e) {
    if (e.key === 'Enter') {
      e.preventDefault()
      this.blur()
    }
  })
}

function handleInputFocus () {
  const chips = this.closest('.chips')
  focus(chips)
}

function handleInputBlur () {
  const chips = this.closest('.chips')
  blur(chips)
}

function handleInputChange (input, config) {
  const { validation } = config
  const chips = input.closest('.chips')
  const value = input.value
  let hasValidValue = true

  if (value === '') {
    return removeError(chips)
  }

  if (validation && !(validation instanceof Function)) {
    return console.error('[Chips] The parameter config.validation must be a function')
  }

  if (validation) {
    hasValidValue = validation(value)
  }

  if (hasValidValue) {
    input.value = ''
    addChip(chips, value, config)
    removeError(chips)
  } else {
    addError(chips)
  }
}

function handleEditChange (input, {
  validation,
  target,
  chip,
  value,
  index
}) {
  const values = target.value.split(';')
  const chips = input.closest('.chips')
  const newValue = input.value
  const eventChange = new Event('change')
  let hasValidValue = true

  blur(chips)

  if (newValue === value) {
    chip.classList.remove('hidden')
    removeError(chips)
    return enableAdd(chips)
  }

  if (newValue === '') {
    removeError(chips)
    removeChip(chip, target)
    return enableAdd(chips)
  }

  if (validation) {
    hasValidValue = validation(newValue)
  }

  if (hasValidValue) {
    // update target values
    values[index] = newValue
    target.value = values.join(';')
    target.dispatchEvent(eventChange)

    // update chip text
    chip.childNodes[0].nodeValue = newValue
    chip.classList.remove('hidden')

    removeError(chips)
    enableAdd(chips)
  } else {
    addError(chips)
  }
}

function handleRemoveChip (target) {
  const chip = this.closest('.chip')
  removeChip(chip, target)
}

function handleEditChip (config) {
  const chip = this.closest('.chip')
  editChip(chip, config)
}

/**
 * Helpers
 */
const getChipIndex = chip =>
  [...chip.closest('.chips').querySelectorAll('.chip')].indexOf(chip)

const getChipsInput = chips =>
  chips.querySelector('.chip-input')

const removeValueFromTarget = (target, index) => {
  const values = target.value.split(';')
  values.splice(index, 1)

  return values.join(';')
}

const sanitizeValue = value =>
  value
    // remove non alphanumeric values, leaving parenthesis, dots, at and spaces
    .replace(/[^[A-z-0-9_@.()\s]+/g, '')
    // remove extra white spaces
    .replace(/\s+/g, ' ')

const createInput = (
  inputPlaceholder = 'Adicionar',
  errorMessage,
  value = ''
) => {
  return `
<div class="chip-input-wrapper" ${errorMessage ? `data-error-msg="${errorMessage}"` : ''}>
  <input type="text" class="chip-input" placeholder="${inputPlaceholder}" value="${value}"/>
</div>`
}

const createChip = (
  value,
  { editable = false, removeable = true } = { editable: false, removeable: true }
) => {
  const editIcon = '<i class="chip-action" data-chip-action="edit"></i>'
  const removeIcon = '<i class="chip-action" data-chip-action="remove"></i>'

  return `
<div class="chip ${editable ? '-editable' : ''}">
  ${value}${editable ? editIcon : ''}${removeable ? removeIcon : ''}
</div>`
    .replace(/[\t\n]/g, '') // remove white spaces and tabs
    .replace(/\s\s+(?=[^><]*<)/g, '') // remove indentation
}

/**
 * Public API
 */
function init (chipsElementSelector, config, context = document) {
  context
    .querySelectorAll(chipsElementSelector)
    .forEach(chips => mount(chips, config))
}

function focus (chips) {
  const input = getChipsInput(chips)

  chips.classList.add('-focus')
  input.focus()
}

function blur (chips) {
  const input = getChipsInput(chips)

  chips.classList.remove('-focus')
  input.blur()
}

function addError (chips) {
  chips.classList.add('-error')
}

function removeError (chips) {
  chips.classList.remove('-error')
}

function addChip (chips, value, config) {
  mountSingleChip(
    chips,
    value,
    config
  )
}

function editChip (chip, config) {
  mountEditChip(
    chip,
    config
  )
}

function removeChip (chip, target) {
  const index = getChipIndex(chip)
  const eventChange = new Event('change')

  target.value = removeValueFromTarget(target, index)
  target.dispatchEvent(eventChange)
  chip.remove()
}

function enableAdd (chips) {
  const inputs = [...chips.querySelectorAll('.chip-input-wrapper')]
  const inputAdd = inputs.pop()

  inputs.forEach(input => input.remove())
  inputAdd.classList.remove('hidden')
  inputAdd.querySelector('.chip-input').focus()
}

function disabledAdd (chips) {
  const inputAdd = [...chips.querySelectorAll('.chip-input-wrapper')].pop()
  inputAdd.classList.add('hidden')
}

export default {
  init,
  focus,
  blur,
  addError,
  removeError,
  addChip,
  editChip,
  removeChip,
  enableAdd,
  disabledAdd
}
