import { addYears } from 'orquestra-helpers'
import DatepickerRange from '../../components/datepicker-range'
import { FormValidation } from '../../components/form-validation'

const SearchForm = new FormValidation([
  {
    element: '[xname=inpperiodoInicio]',
    errorMessage: 'Informe o perodo para pesquisa'
  }, {
    element: '[xname=inpperiodoFim]',
    errorMessage: 'Informe o perodo para pesquisa'
  }
])

const ExamsForm = function () {
  const $refs = {
    section: document.querySelector('#section-exams-form'),
    periodStart: document.querySelector('[xname=inpperiodoInicio]'),
    periodEnd: document.querySelector('[xname=inpperiodoFim]'),
    searchBtn: document.querySelector('.js-search-lives')
  }

  const state = {
    emitter: null
  }

  /**
   * Public Methods
   */
  function mount ({ emitter }) {
    state.validator = SearchForm
    state.emitter = emitter

    $refs.searchBtn
      .addEventListener('click', handleSearch)

    $refs.section
      .classList.remove('hidden')

    DatepickerRange.init('#exams-period', {
      startDate: addYears(new Date(), -2),
      endDate: addYears(new Date(), 2),
      startView: 'months',
      minViewMode: 'months',
      format: 'M/yyyy'
    })
  }

  function getPeriod () {
    return {
      start: $($refs.periodStart).datepicker('getDate'),
      end: $($refs.periodEnd).datepicker('getDate')
    }
  }

  function getTrigger () {
    return $refs.searchBtn
  }

  function validate ({ silent } = { silent: false }) {
    return SearchForm.validate({ silent })
  }

  /**
   * Private Methods
   */
  function handleSearch (e) {
    const { hasErrors } = validate()

    e.preventDefault()

    if (hasErrors) {
      return hasErrors
    }

    state.emitter.emit('search-start')
  }

  return {
    mount,
    getPeriod,
    getTrigger,
    validate
  }
}

export default ExamsForm()
