import { TableMultivalue } from '../../components/table-multivalue'
import TextareaAutosize from '../../components/textarea-autosize'
import ButtonRadio from '../../components/button-radio'
import DatepickerRange from '../../components/datepicker-range'

const TableActions = function () {
  const tableSelector = '#tbl-actions'
  const fieldDecriptionId = 'planoAcompanhamento'

  const $refs = {
    section: document.querySelector('#section-actions'),
    table: document.querySelector(tableSelector),
    executedLabels: document.querySelectorAll('[xid=divplanoExecutado]'),
    periodStart: document.querySelector('[xname=inpperiodoInicio]'),
    pertiodEnd: document.querySelector('[xname=inpperiodoFim]')
  }

  const state = {
    alias: null
  }

  /**
   * Public Methods
   */
  function mount ({ alias, hasToResetTable }) {
    const isReadonly = ['complementacao', 'complementacaoAnual'].includes(alias)
    const isStatic = ['complementacao'].includes(alias)

    state.alias = alias

    if (isReadonly) {
      return setTableReadonly()
    }

    const Table = new TableMultivalue(tableSelector, onRowMount)

    if (isStatic) {
      Table.static()
    }

    if (hasToResetTable) {
      Table.reset()
    }

    return Table
  }

  function hasToMount ({ alias }) {
    return [
      'elaboracao',
      'complementacao',
      'elaboracaoAnual',
      'complementacaoAnual'
    ].includes(alias)
  }

  function show () {
    $refs.section
      .classList
      .remove('hidden')
  }

  function destroy () {
    $refs.section.remove()
  }

  /**
   * Private Methods
   */
  function onRowMount (row) {
    ButtonRadio
      .init('.btn-radio-wrapper', true, row)

    TextareaAutosize(
      row.querySelector(`[xname=inp${fieldDecriptionId}]`)
    )

    DatepickerRange
      .init('.input-monthrange', {
        startView: 'months',
        minViewMode: 'months',
        format: 'M/yyyy'
      }, row)
  }

  function setTableReadonly () {
    $refs.table
      .classList
      .add('-readonly', '-hover')

    $refs.executedLabels
      .forEach(appendTag)

    DatepickerRange
      .init(`${tableSelector} .input-monthrange`, {})
  }

  function appendTag ($label) {
    // ignore label ordering prefix: 2.Parcial
    const text = $label.textContent.substring(2)
    const $td = $label.closest('td')
    const $btnRadio = $td.querySelector('.btn-radio-wrapper')
    const tagMarkup = createTag(text)

    $td.insertAdjacentHTML('beforeend', tagMarkup)
    $btnRadio.classList.add('hidden')
    $label.classList.add('hidden')
  }

  return {
    mount,
    hasToMount,
    show,
    destroy
  }
}

export default TableActions()

/**
 * Helpers
 */
const createTag = text =>
`<span class="tag ${getLabelModifier(text)}">${text}</span>`

const getLabelModifier = text => {
  if (text === 'Sim') {
    return '-success'
  }
  if (text === 'No') {
    return '-danger'
  }
  if (text === 'Parcial') {
    return '-warning'
  }

  console.error(`[Module:TableActions] Unknow executed value: ${text}`)
  return ''
}
