import Tabs from './../../components/tabs'

const ExamsTabs = function () {
  const tabsSelector = '#exams-tabs'

  const $refs = {
    tabsPane: document.querySelector('#exams-search-panes'),
    tabs: document.querySelectorAll(`${tabsSelector} .tab`)
  }

  const state = {
    emitter: null
  }

  /**
   * Public Methods
   */
  function mount ({ emitter }) {
    state.emitter = emitter
    state.emitter.on('search-done', enableAllTabs)
    state.emitter.on('tab-data-found', pulseTab)

    Tabs.init(tabsSelector)
    setInitialState()
  }

  function getTabsPane () {
    return $refs.tabsPane
  }

  function getTabs () {
    return $refs.tabs
  }

  /**
   * Private Methods
   */
  function setInitialState () {
    $refs.tabs.forEach((tab, index) => {
      if (index > 0) {
        return Tabs.disabledTab(tab)
      }

      Tabs.enableTab(tab)
    })
  }

  function enableAllTabs () {
    $refs.tabs.forEach(tab => {
      Tabs.enableTab(tab)
    })
  }

  function pulseTab (tab) {
    Tabs.pulseTab(tab)
  }

  return {
    mount,
    getTabsPane,
    getTabs
  }
}

export default ExamsTabs()
