import TableCollapse from '../../components/table-collapse'
import { appendData } from '../../components/table-multivalue'
import ExamsTableConfig from './exams-table-config'
import Coordination from './api/api.coordination'
import HelpPlus from './api/api.help-plus'

const ExamsTables = function () {
  const $refs = {
    templateHelp: document.querySelector('#template-help'),
    templateCoord: document.querySelector('#template-coordenacao')
  }

  const state = {
    alias: null,
    emitter: null,
    productType: null
  }

  /**
   * Public Methods
   */
  function mount ({ emitter, alias, productType }) {
    state.alias = alias
    state.productType = productType
    state.emitter = emitter
    state.emitter.on('search-done', createTables)
  }

  /**
   * Private Methods
   */
  function createTables ([livesData, helpData, coordData]) {
    const {
      lives,
      help,
      coord
    } = ExamsTableConfig.get(state.productType)

    build({
      data: livesData,
      ...lives
    })

    build({
      data: helpData,
      ...help
    })

    build({
      data: coordData,
      ...coord
    })

    if (['complementacao'].includes(state.alias)) {
      persistData({ helpData, coordData })
    }

    TableCollapse.init('.table-collapse')
  }

  function persistData ({ helpData, coordData }) {
    appendData(
      $refs.templateHelp,
      HelpPlus.formatData(helpData)
    )

    appendData(
      $refs.templateCoord,
      Coordination.formatData(coordData)
    )
  }

  function build ({
    data,
    columns,
    tabName,
    tableId = '',
    emptyMessage = 'No foi encontrada nenhuma informao no perodo pesquisado.'
  }) {
    const tabContainer = document.querySelector(`.tab-pane[data-tab="${tabName}"]`)
    const tabNav = document.querySelector(`.tab[data-tab="${tabName}"]`)

    if (!tabContainer) {
      return console.error(`[Module:ExamsTables] Container element not found for tab: ${tabName}`)
    }

    if (!data.length) {
      const emptyHtml = createEmptyMessage(emptyMessage)
      tabContainer.innerHTML = emptyHtml

      return false
    }

    const yearlyData = groupDataByYear(data, columns)
    const headerSettings = columns.map(column => {
      return {
        showTotal: true,
        showAverage: true,
        headerPrefix: '',
        total: yearlyData.summary[column.key],
        average: yearlyData.summary[column.key] / yearlyData.summary.rows,
        ...column
      }
    })

    const tableHeaderHtml = createTableHeader(headerSettings)

    const tableCollapseHtml = Object.entries(yearlyData.entries)
      .map(([year, rows]) => createCollapse(year, rows, columns))
      .join('')

    const tableHtml = `<table class="table-collapse -tree -padded -hover" id="${tableId}">${tableHeaderHtml}${tableCollapseHtml}</table>`
      .replace(/[\t\n]/g, '') // remove white spaces and tabs
      .replace(/\s\s+(?=[^><]*<)/g, '') // remove indentation

    tabContainer.innerHTML = tableHtml

    state.emitter.emit('tab-data-found', tabNav)
  }

  return {
    mount
  }
}

export default ExamsTables()

/**
 * Helpers
 */
const createEmptyMessage = message => {
  return `
<div class="empty">
<div class="empty-icon">
  <i class="fas fa-calendar -minus"></i>
</div>
<div class="empty-content">
  <h1 class="empty-title">Nenhuma informao no perodo</h1>
  <p class="empty-message">${message}</p>
</div>
</div>`
}

const createCollapse = (title, rows, columns) => {
  const collapseSettings = columns.map(column => {
    const total = rows.reduce((acc, data) => acc + data[column.key], 0)
    const average = total / rows.length

    const columnSettings = {
      showTotal: true,
      showAverage: true,
      average,
      total,
      ...column
    }

    const collapsePrefix = crateCollapsePrefix(columnSettings)

    return {
      ...columnSettings,
      collapsePrefix
    }
  })

  const collapseHeader = createCollapseHeader(title, collapseSettings)
  const collapseRows = createCollapseGroup(rows, columns)

  return collapseHeader + collapseRows
}

const crateCollapsePrefix = column => {
  if (!column.showTotal && !column.showAverage) {
    return ''
  }

  const averageLabel = '<span class="text-summary">(Mdia)</span>'
  const totalLabel = '<span class="text-sub">Total</span>'

  return `
  <span class="margin-right-m">
    ${column.showAverage ? averageLabel : ''}
    ${column.showTotal ? totalLabel : ''}
  </span>`
}

const createTableHeader = columns => {
  const cols = columns.map(col => createTableHeaderColumn(col))

  return `
<thead>
  <tr class="-stick">
    <th></th>
    ${cols.join('')}
    <th></th>
  </tr>
</thead>`
}

const createTableHeaderColumn = ({
  title,
  total,
  average,
  headerPrefix,
  showTotal,
  showAverage
}) => {
  return `
<th class="text-right">
  <div class="col">${headerPrefix}${title}</div>
  <div class="col ${!showAverage && !showTotal ? 'hidden' : ''}">
    <span class="text-small text-summary margin-right-s ${!showAverage ? 'hidden' : ''}">
    ${total > 0 ? '(' + average.toLocaleString('pt-BR', { minimumFractionDigits: 1, maximumFractionDigits: 1 }) + ')' : ''}</span>
    <span class="text-small ${!showTotal ? 'hidden' : ''}">${total > 0 ? total : ''}</span>
  </div>
</th>`
}

const createCollapseHeader = (title, columns) => {
  const cols = columns.map((col, i) => createCollapseHeaderColumn(col, i))

  return `
<tbody class="table-collapse-header -stick -summary">
  <tr>
    <th><i class="table-collapse-indicator fas fa-chevron-down"></i>${title}</th>
    ${cols.join('')}
    <th></th>
  </tr>
</tbody>`
}

const createCollapseHeaderColumn = ({
  total,
  average,
  collapsePrefix,
  showTotal,
  showAverage
}, columnIndex) => {
  return `
<th class="text-right">
  ${columnIndex === 0 ? collapsePrefix : ''}
  ${showAverage && average > 0 ? '<span class="text-summary margin-right-s">(' +
    average.toLocaleString('pt-BR', { minimumFractionDigits: 1, maximumFractionDigits: 1 }) +
  ')</span>' : ''}
  ${showTotal
      ? total > 0
        ? total
        : '-'
      : ''
    }
</th>`
}

const createCollapseGroup = (rows, columns) => {
  const tableRows = rows.map(row => createCollapseRow(row, columns))

  return `
<tbody class="table-collapse-group">
  ${tableRows.join('')}
</tbody>`
}

const createCollapseRow = (row, columns) => {
  const cols = columns.map(({ key }) => `<td class="text-right">${row[key] > 0 ? row[key] : '-'}</td>`)

  return `
<tr>
  <td>${row.monthName}</td>
  ${cols.join('')}
  <td></td>
</tr>
  `
}

const groupDataByYear = (data, columns) => {
  const years = new Set(
    data.map(({ year }) => year)
  )

  const summary = data.reduce((acc, data, row) => {
    columns.forEach(({ key }) => {
      if (!(key in data)) {
        return
      }
      if (row === 0) {
        acc[key] = data[key]
      } else {
        acc[key] += data[key]
      }
    })
    return acc
  }, {})

  summary.rows = data.length

  const entries = {}

  years.forEach(year => {
    const yearlyData = data.filter(entry => entry.year === year)
    entries[year] = yearlyData
  })

  return {
    entries,
    summary
  }
}
