const ReportType = function () {
  const $refs = {
    sendReport: {
      group: document.querySelector('#send-report'),
      field: document.querySelector('[xname=inpenviarEmail]')
    },
    reportType: {
      group: document.querySelector('#report-type'),
      field: document.querySelectorAll('[xname=inptipoTemplate]')
    }
  }

  const state = {
    emitter: null
  }

  /**
   * Public Methods
   */
  function mount ({ emitter }) {
    state.emitter = emitter

    $refs.sendReport.group
      .classList
      .remove('hidden')

    $refs.reportType.group
      .classList
      .remove('hidden')

    $refs.sendReport.field
      .addEventListener('change', onChange)

    onChange.call($refs.sendReport.field)
  }

  /**
   * Private Methods
   */
  function onChange () {
    state.emitter.emit('send-report-change', this.checked)

    return this.checked
      ? enableOptions()
      : disabledOptions()
  }

  function enableOptions () {
    $refs.reportType.field.forEach(option => {
      option.disabled = false
      option.setAttribute('required', 'S')
    })
  }

  function disabledOptions () {
    $refs.reportType.field.forEach(option => {
      option.disabled = true
      option.checked = false
      option.setAttribute('required', 'N')
    })
  }

  return {
    mount
  }
}

export default ReportType()
