function mount (table) {
  table
    .querySelectorAll('.table-collapse-header th:first-child')
    .forEach(tableGroupTitle => setTriggers(tableGroupTitle))
}

function setTriggers (tableGroupTitle) {
  tableGroupTitle.addEventListener('click', handleClick)
}

function handleClick () {
  const tableGroupHeader = this.closest('.table-collapse-header')

  if (!tableGroupHeader) {
    return console.error('[Table Collapse] .table-collapse-header not found, must have a parent .table-collapse-header')
  }

  toggleGroup(tableGroupHeader)
}

/**
 * Public API
 */
function init (tableElementSelector, context = document) {
  context
    .querySelectorAll(tableElementSelector)
    .forEach(table => mount(table))
}

function expand (table) {
  table
    .querySelectorAll('.table-collapse-header')
    .forEach(tableGroupHeader => showGroup(tableGroupHeader))
}

function shrink (table) {
  table
    .querySelectorAll('.table-collapse-header')
    .forEach(tableGroupHeader => hideGroup(tableGroupHeader))
}

function destroy (table) {
  expand(table)

  table
    .querySelectorAll('.table-collapse-header th:first-child')
    .forEach(tableGroupTitle => {
      tableGroupTitle.removeEventListener('click', handleClick)
    })
}

function toggleGroup (tableGroupHeader) {
  tableGroupHeader.classList.toggle('-collapse')
}

function showGroup (tableGroupHeader) {
  tableGroupHeader.classList.remove('-collapse')
}

function hideGroup (tableGroupHeader) {
  tableGroupHeader.classList.add('-collapse')
}

export default {
  init,
  expand,
  shrink,
  destroy,
  toggleGroup,
  showGroup,
  hideGroup,
  /**
   * `.group` aliases
   */
  group: {
    toggle: toggleGroup,
    show: showGroup,
    hide: hideGroup
  }
}
