import {
  addMonths,
  parseDate,
  getMonthName
} from 'orquestra-helpers'
import { TableMultivalue } from '../../components/table-multivalue'
import Template from '../../components/template'

const TableOccupationalExams = function () {
  const tableSelector = '#tbl-exams'

  const tableFieldsId = {
    normal: 'examesNormais',
    altered: 'examesAlterados',
    total: 'examesTipoTotal'
  }

  const $refs = {
    section: document.querySelector('#section-exams'),
    table: document.querySelector(tableSelector),
    tableSummary: document.querySelector('#tbl-exames-total'),
    alert: document.querySelector('#exams-alert'),
    reportDate: document.querySelector('[xname=inpdataCicloRelatorio]')
  }

  const state = {
    alias: null,
    productType: null
  }

  /**
   * Public Methods
   */
  function mount ({ alias, productType, hasToResetTable }) {
    const isReadonly = [
      'complementacao',
      'elaboracaoAnual',
      'complementacaoAnual'
    ].includes(alias)

    const hasAlert = [
      'elaboracaoAnual',
      'complementacaoAnual'
    ].includes(alias) && productType === 'PCMSO'

    state.alias = alias

    if (hasAlert) {
      setAlertMessage()
    }

    if (isReadonly) {
      return setTableReadonly()
    }

    const Table = new TableMultivalue(tableSelector, onRowMount)

    if (hasToResetTable) {
      Table.reset()
    }

    Table.on('beforeDelete', onRowDelete)
    Table.on('afterDelete', updateSummary)

    $refs.summary = {
      totalNormal: $refs.tableSummary.querySelector('[xname=inpexamesTotalNormal]'),
      totalAltered: $refs.tableSummary.querySelector('[xname=inpexamesTotalAlterado]'),
      total: $refs.tableSummary.querySelector('[xname=inpexamesTotalGlobal]')
    }
  }

  function hasToMount ({ alias, productType }) {
    return [
      'elaboracao',
      'complementacao',
      'elaboracaoAnual',
      'complementacaoAnual'
    ].includes(alias) && productType === 'PCMSO'
  }

  function show () {
    $refs.section
      .classList
      .remove('hidden')
  }

  function destroy () {
    $refs.section.remove()
  }

  /**
   * Private Methods
   */
  function onRowMount (row) {
    const totalLabel = row.querySelector(`[data-bind=${tableFieldsId.total}]`)
    const totalInput = getRowTotalInput(row)
    const {
      examesNormais,
      examesAlterados
    } = getTableRowFields(row)

    examesNormais.input.addEventListener('change', onInputChange)
    examesAlterados.input.addEventListener('change', onInputChange)

    Template.mount(totalInput)

    // Orquestra `InsertNewRow` clone the 1st row with all values
    // So we need to rewrite the total value with `Template.update`
    Template.update(totalLabel)
  }

  function onInputChange () {
    const changeEvent = new Event('change')
    const row = this.closest('tr')
    const {
      examesNormais,
      examesAlterados,
      examesTipoTotal
    } = getTableRowFields(row)

    examesTipoTotal.input.value = examesAlterados.value + examesNormais.value
    examesTipoTotal.input.dispatchEvent(changeEvent)

    updateSummary()
  }

  function onRowDelete (e) {
    const { deletedRow } = e.detail
    const totalInput = getRowTotalInput(deletedRow)

    Template.remove(totalInput)
  }

  function updateSummary () {
    const changeEvent = new Event('change')
    const { total } = getTableData()

    $refs.summary.totalNormal.value = total.examesNormais
    $refs.summary.totalAltered.value = total.examesAlterados
    $refs.summary.total.value = total.examesTipoTotal

    Object.values($refs.summary)
      .forEach(input => input.dispatchEvent(changeEvent))
  }

  function getRowTotalInput (row) {
    return row.querySelector(`[xname=inp${tableFieldsId.total}]`)
  }

  function getTableData () {
    const rows = [...$refs.table.querySelectorAll('tr:not(.header)')]
      .map(getTableRowFields)

    const total = rows.reduce((total, row) => {
      Object.entries(row)
        .forEach(([id, field]) => {
          if (!total[id]) {
            total[id] = field.value
          } else {
            total[id] += field.value
          }
        })
      return total
    }, {})

    return {
      rows,
      total
    }
  }

  function getTableRowFields (row) {
    return Object.values(tableFieldsId)
      .reduce((fields, fieldId) => {
        const input = row.querySelector(`[xname=inp${fieldId}]`)
        const value = parseInt(input.value) || 0

        fields[fieldId] = {
          input,
          value
        }

        return fields
      }, {})
  }

  function setTableReadonly () {
    $refs.table
      .classList
      .add('-readonly', '-hover')
  }

  function setAlertMessage () {
    const reportDate = parseDate($refs.reportDate.value)

    $refs.alert.textContent = createAlertMessage(reportDate)
  }

  return {
    mount,
    hasToMount,
    show,
    destroy
  }
}

export default TableOccupationalExams()

/**
 * Helpers
 */
const createAlertMessage = reportDate => {
  const previousReport = addMonths(reportDate, -5)
  const month = getMonthName(previousReport)
  const year = previousReport.getFullYear()

  return `Os dados abaixo so referentes ao relatrio semestral elaborado em ${month}/${year}.`
}
