import ExamsTabs from './exams-tabs'
import ExamsForm from './exams-form'
import ExamsControls from './exams-controls'
import ExamsTables from './exams-tables'
import keys from './keys'

/**
 * Services
 */
import SPlusService from './api/api.s-plus'
import LivesSerice from './api/api.lives'
import CoordinationService from './api/api.coordination'
import HelpPlusService from './api/api.help-plus'

const ExamsSearch = function () {
  const $refs = {
    companyId: document.querySelector('[xname=inpcodEmpresaSmais]'),
    companyCnpj: document.querySelector('[xname=inpcnpjEmpresaDeAtendimento]'),
    proposalId: document.querySelector('[xname=inpnumeroDaProposta]'),
    productId: document.querySelector('[xname=inpcodProduto]'),
    productType: document.querySelector('[xname=inptipoProduto]'),
    unityName: document.querySelector('[xname=inpunidadeProspectora]'),
    trigger: ExamsForm.getTrigger(),
    pane: ExamsTabs.getTabsPane()
  }

  const state = {
    alias: null,
    emitter: null,
    companyCnpj: $refs.companyCnpj.value,
    proposalId: $refs.proposalId.value,
    productId: $refs.productId.value,
    productType: $refs.productType.value
  }

  /**
   * Public Methods
   */
  function init ({ alias, emitter }) {
    const mountComponents = [
      'elaboracao',
      'complementacao',
      'elaboracaoAnual',
      'complementacaoAnual'
    ].includes(alias)

    state.emitter = emitter
    state.alias = alias
    state.emitter.on('search-start', searchData)

    if (mountComponents) {
      mount()
    }
  }

  function mount () {
    const { hasErrors } = ExamsForm.validate({ silent: true })
    const {
      alias,
      emitter,
      productType
    } = state

    ExamsForm.mount({ emitter })
    ExamsTabs.mount({ emitter })
    ExamsTables.mount({ emitter, alias, productType })

    if (state.alias === 'elaboracao') {
      ExamsControls.mount({ emitter })
    }

    if (!hasErrors) {
      state.emitter.emit('search-start')
    }
  }

  /**
   * Private Methods
   */
  function searchData () {
    const unitName = $refs.unityName.value
    const keys = getUnityKeys(unitName)
    const cnpj = state.companyCnpj

    addLoadingState()

    Promise.resolve({ cnpj, keys })
      .then(getCompanyId)
      .then(companyId => {
        $refs.companyId.value = companyId
        return { ...keys, companyId }
      })
      .then(getExamsData)
      .then(handleSearchDone)
      .catch(handleSearchError)
      .finally(removeLoadingState)
  }

  function getCompanyId ({ cnpj, keys }) {
    const companyId = $refs.companyId.value

    if (companyId !== '') {
      return companyId
    }

    return SPlusService
      .getDataFromDataSource({
        cnpj,
        unitId: keys.id,
        exportKey: keys.new_exportKey,
        apiKey: keys.new_key
      })
  }

  function getExamsData ({ exportKey, key, companyId }) {
    const { start, end } = ExamsForm.getPeriod()

    const livesData = LivesSerice.getDataFromPeriod({
      start,
      end,
      exportKey,
      companyId,
      apiKey: key
    })

    const helpPlusData = HelpPlusService.getDataFromPeriod({
      start,
      end,
      proposal: state.proposalId,
      productId: state.productId
    })

    const coordinationData = CoordinationService.getDataFromPeriod({
      start,
      end,
      cnpj: state.companyCnpj
    })

    return Promise.all([
      livesData,
      helpPlusData,
      coordinationData
    ])
  }

  function handleSearchDone (searchData) {
    state.emitter.emit('search-done', searchData)
  }

  function handleSearchError (err) {
    console.error('[Module:Exams] Something went wrong during the search')
    console.error(err)
  }

  function addLoadingState () {
    [$refs.trigger, $refs.pane]
      .forEach(el =>
        el.classList.add('-loading')
      )
  }

  function removeLoadingState () {
    [$refs.trigger, $refs.pane]
      .forEach(el =>
        el.classList.remove('-loading')
      )
  }

  return {
    init
  }
}

export default ExamsSearch()

/**
 * Helpers
 */
function getUnityKeys (unitName) {
  const data = Object.values(keys)
    .find(unit => unit.name === unitName)

  if (!data) {
    console.error(`[Module:Exams] Unit not found: ${unitName}`)
    return null
  }

  return data
}
