function mount (list) {
  const toggleItems = list.querySelectorAll('li:not(:first-child)')
  const listToggler = createListToggler(toggleItems.length)

  list.insertAdjacentHTML('beforeend', listToggler)

  setTriggers(list)
}

function setTriggers (list) {
  const togglers = list.querySelectorAll('.list-trunk-toggler')

  togglers
    .forEach(toggler => toggler.addEventListener('click', handleClick))
}

function handleClick () {
  const list = this.closest('ul')
  list.classList.toggle('-active')
}

/**
 * Helpers
 */
const createListToggler = length => {
  return `
<span class="list-trunk-toggler" data-trunk="more">Mostrar mais ${length}</span>
<span class="list-trunk-toggler" data-trunk="less">Mostrar menos</span>
`
}

/**
 * Public API
 */
function init (listElementSelector, context = document) {
  context
    .querySelectorAll(listElementSelector)
    .forEach(list => mount(list))
}

export default {
  init
}
