import TableActions from './table-actions'
import TableRecommendations from './table-recommendations'
import TableOccupationalExams from './table-occupational-exams'

const Tables = function () {
  const $refs = {
    productType: document.querySelector('[xname=inptipoProduto]'),
    periodStart: document.querySelector('[xname=inpperiodoInicio]'),
    pertiodEnd: document.querySelector('[xname=inpperiodoFim]')
  }

  const state = {
    alias: null,
    emitter: null,
    productType: $refs.productType.value
  }

  /**
   * Public Methods
   */
  function init ({ alias, emitter }) {
    const hasToInit = [
      'elaboracao',
      'complementacao',
      'elaboracaoAnual',
      'complementacaoAnual'
    ].includes(alias)

    state.alias = alias
    state.emitter = emitter

    if (!hasToInit) {
      return
    }

    state.emitter.on('search-done', showTables)

    state.tables = [
      TableActions,
      TableRecommendations,
      TableOccupationalExams
    ].filter(initTables)
  }

  /**
   * Private Methods
   */
  function initTables (table) {
    const props = {
      alias: state.alias,
      productType: state.productType,
      hasToResetTable: evalTableReset(state.alias)
    }

    const hasToMount = table.hasToMount(props)

    if (!hasToMount) {
      table.destroy()
    } else {
      table.mount(props)
    }

    return hasToMount
  }

  function showTables () {
    state.tables
      .forEach(table => table.show())
  }

  function evalTableReset (alias) {
    const isOnInitialTask = ['elaboracao'].includes(alias)
    const isPeriodEmpty = $refs.periodStart.value === '' && $refs.pertiodEnd.value === ''

    return isOnInitialTask && isPeriodEmpty
  }

  return {
    init
  }
}

export default Tables()
