import {
  getMonthInt,
  getMonthNameByNumber
} from 'orquestra-helpers'

import { fillMonthsOnPeriod } from './util'

const ORQUESTRA_DATASOURCE_URL = process.env.DATASOURCE_BASE_URL + process.env.DATASOURCE_HELP_PLUS

const config = {
  defaultParams: {
    quantityDoubts: 0,
    quantityChanges: 0,
    quantitySupportSPlus: 0,
    quantitySupportCoord: 0,
    quantitySupportVivaPlus: 0
  }
}

const getDataFromDataSource = function ({
  start,
  end,
  proposal,
  productId
}) {
  const url = new URL(ORQUESTRA_DATASOURCE_URL)
  const params = {
    inpperiodoInicial: getMonthInt(start),
    inpanoInicial: start.getFullYear(),
    inpperiodoFinal: getMonthInt(end),
    inpanoFinal: end.getFullYear(),
    inpnumeroDaProposta: proposal,
    inpcodProduto: productId
  }

  url.search = new URLSearchParams(params).toString()

  return fetch(url)
    .then(res => res.json())
    .then(({ success }) => {
      const data = parseData(success)

      return {
        data,
        start,
        end
      }
    })
}

const parseData = success => {
  const data = success.map(({ fields }) => {
    const result = {
      ...config.defaultParams,
      month: parseInt(fields.mes),
      monthName: getMonthNameByNumber(fields.mes),
      year: parseInt(fields.ano)
    }

    const quantityKey = getQuantityKeyByType(fields.tipoDeDemanda)
    result[quantityKey] = parseInt(fields.quantidadeTipo)

    return result
  })

  return data
}

const getQuantityKeyByType = type => {
  const prefix = 'quantity'
  let keyType = ''

  switch (type) {
    case 'Dvida':
      keyType = 'Doubts'
      break

    case 'Mudana nos Programas/Documentos/Exames':
      keyType = 'Changes'
      break

    case 'Suporte para o Sistema Informatizado S+':
      keyType = 'SupportSPlus'
      break

    case 'Suporte do Mdico Coordenador do PCMSO':
      keyType = 'SupportCoord'
      break

    case 'Suporte Portal SESI Viva+':
      keyType = 'SupportVivaPlus'
      break
  }

  return prefix + keyType
}

const groupDataByType = data => {
  const years = new Set(
    data.map(({ year }) => year)
  )

  let result

  years.forEach(year => {
    const yearlyData = data.filter(entry => entry.year === year)
    const months = [...new Set(
      yearlyData.map(({ month }) => month)
    )]

    result = months.map(month => {
      return data
        .filter(entry => entry.month === month)
        .reduce((acc, curr) => Object.assign(acc, curr), {})
    })
  })

  return result
}

function getDataFromPeriod (params) {
  return new Promise((resolve, reject) => {
    getDataFromDataSource(params)
      .then(({ start, end, data }) => {
        if (!data.length) {
          return resolve(data)
        }

        const { defaultParams } = config
        const mergedData = groupDataByType(data)
        const filledData = fillMonthsOnPeriod({
          start,
          end,
          defaultParams,
          data: mergedData
        })

        return resolve(filledData)
      })
      .catch(err => reject(err))
  })
}

function formatData (data) {
  return data.map(entry => {
    const rowData = {}
    rowData.helpMesAno = `${entry.monthName.substring(0, 3)}/${entry.year}`
    rowData.helpDuvidas = entry.quantityDoubts || '-'
    rowData.helpMudancas = entry.quantityChanges || '-'
    rowData.helpSuporteSMais = entry.quantitySupportSPlus || '-'
    rowData.helpSuporteMedCoordenacao = entry.quantitySupportCoord || '-'
    rowData.helpSuporteVivaMais = entry.quantitySupportVivaPlus || '-'

    return rowData
  })
}

export default {
  getDataFromPeriod,
  formatData
}
