import {
  getMonthInt,
  getMonthName
} from 'orquestra-helpers'

const ORQUESTRA_DATASOURCE_URL = process.env.DATASOURCE_BASE_URL + process.env.DATASOURCE_LIVES

const getDataFromDataSource = function ({
  date,
  companyId,
  exportKey,
  apiKey
}) {
  const year = date.getFullYear()
  const monthName = getMonthName(date)
  const url = new URL(ORQUESTRA_DATASOURCE_URL)
  const params = {
    inpano: year,
    inpmes: getMonthInt(date),
    inpempresaDeAtendimento: companyId,
    inpcodExportaDados: exportKey,
    inpchaveExporta: apiKey
  }

  url.search = new URLSearchParams(params).toString()

  return fetch(url)
    .then(res => res.json())
    // # Observao:
    // WS SOAP do SOC sempre retornar 1 elemento via Fonte de Dados
    // Orquestra. O retorno de sucesso  sempre uma string JSON
    // deserializada, por isso a necessidade de `JSON.parse`
    .then(({ success }) => {
      if (success[0].fields.error === 'true') {
        console.info(success[0].fields.msg)
        return null
      }
      return JSON.parse(success[0].txt)
    })
    .then(data => {
      const result = parseData(data)

      return {
        ...result,
        year,
        monthName
      }
    })
}

const parseData = data => {
  if (!data) {
    return {
      quantityActiveLives: 0,
      quantityInactiveLives: 0
    }
  }

  const livesCount = data.reduce((acc, company) => {
    if (company.situacaoFuncionario === 'Ativo') {
      acc.quantityActiveLives += 1
    } else {
      acc.quantityInactiveLives += 1
    }
    return acc
  }, {
    quantityActiveLives: 0,
    quantityInactiveLives: 0
  })

  return livesCount
}

function getDataFromPeriod ({
  start,
  end,
  companyId,
  exportKey,
  apiKey
}) {
  const data = []
  const currentDate = new Date(start.getTime())

  // eslint-disable-next-line no-unmodified-loop-condition
  while (currentDate <= end) {
    const monthData = getDataFromDataSource({
      date: currentDate,
      companyId,
      exportKey,
      apiKey
    })

    data.push(monthData)
    currentDate.setMonth(currentDate.getMonth() + 1)
  }

  return new Promise((resolve, reject) => {
    Promise.all(data)
      .then(months => resolve(months))
      .catch(err => reject(err))
  })
}

export default {
  getDataFromPeriod
}
