/**
 * Dependencies
 */
import mitt from 'mitt'
import { getTaskAlias } from 'orquestra-helpers'

/**
 * Components
 */
import Template from './components/template'
import Switch from './components/switch'
import RadioCard from './components/radio-card'

/**
 * Modules
 */
import ExamsSearch from './modules/exams-search/index'
import Tables from './modules/tables'
import TaskReviewer from './modules/task-reviewer'
import Reports from './modules/reports'

const emitter = mitt()
const alias = getTaskAlias()

Template.init({
  filters: {
    product (value) {
      const [initials, desc] = value.split('-')
      return `${initials.toUpperCase()} - ${this.titleCase(desc)}`
    }
  }
})
Template.conditionals()
Switch.init('.switch')
RadioCard.init('.radio-card')

/**
 * Modules
 */
TaskReviewer.init()
Reports.init({ alias, emitter })
Tables.init({ alias, emitter })
ExamsSearch.init({ alias, emitter })
