const ORQUESTRA_DATASOURCE_URL = process.env.DATASOURCE_BASE_URL + process.env.DATASOURCE_S_PLUS

const getDataFromDataSource = function ({
  cnpj,
  unitId,
  exportKey,
  apiKey
}) {
  const url = new URL(ORQUESTRA_DATASOURCE_URL)

  const params = {
    inpcodUnidade: unitId,
    inpcodExportaDadosNovo: exportKey,
    inpchaveExportaNovo: apiKey
  }

  url.search = new URLSearchParams(params).toString()

  return fetch(url)
    .then(res => res.json())
    // Disclaimer:
    // WS SOAP do SOC sempre retornar 1 elemento via Fonte de Dados
    // Orquestra. O retorno de sucesso  sempre uma string JSON
    // deserializada, por isso a necessidade de `JSON.parse`
    .then(({ success }) => {
      if (success[0].fields.error === 'true') {
        throw Error(success[0].fields.msg)
      }
      return JSON.parse(success[0].txt)
    })
    .then(data => {
      const company = findCompany(data, cnpj)

      if (!company) {
        alert(`Infelizmente no  possvel realizar a sua pesquisa.\n\nNo foi encontrada nenhuma empresa com o seguinte CNPJ no S+:\n${cnpj}`)
        throw Error('Empresa no encontrada para o CNPJ informado')
      }

      return company.CODIGO
    })
}

const findCompany = (data, cnpj) =>
  data.find(company => {
    return cnpj.replace(/\D/g, '') === company.CNPJ.replace(/\D/g, '') && company.ATIVO === '1'
  })

export default {
  getDataFromDataSource
}
