function mount (container, datepickerOptions = {}) {
  const inputs = $(container).find('[xname]')
  const labels = $(container).find('[xid]')

  const options = {
    language: 'pt-BR',
    inputs,
    ...datepickerOptions
  }

  if (labels.length > 0) {
    container.classList.add('-readonly')
  }

  if (inputs.length === 0) {
    return console.error(`[DatepickerRange] Orquestra inputs not found inside ${container}`)
  }

  inputs.attr('autocomplete', 'off')
  $(container).datepicker(options)
}

/**
 * Public API
 */
function init (containerElementSelector, datepickerOptions, context = document) {
  context
    .querySelectorAll(containerElementSelector)
    .forEach(container => mount(container, datepickerOptions))
}

export default {
  init
}
