function mount (switchEl) {
  const label = switchEl.querySelector('label')
  const field = switchEl.querySelector('[xname]')
  const isReadOnly = label === null
  const isActive = field.value !== ''
  const marker = getSwitchMarker(isReadOnly, isActive)

  if (isReadOnly) {
    return switchEl.insertAdjacentHTML('beforeend', marker)
  }

  label.insertAdjacentHTML('beforeend', marker)
}

const getSwitchMarker = (readonly, active) =>
  `<div
    class="switch-marker ${readonly ? '-readonly' : ''} ${active && readonly ? '-active' : ''}">
  </div>`

/**
 * Public API
 */
function init (switchElementSelector, context = document) {
  context
    .querySelectorAll(switchElementSelector)
    .forEach(switchEl => mount(switchEl))
}

export default {
  init
}
