const TaskReviewer = function () {
  const $refs = {
    reviwerCodUser: document.querySelector('[xname=inpprofissionalAprovador]'),
    ppraCodUser: document.querySelector('[xname=inpengenheiroTxt]'),
    pcmsoCodUser: document.querySelector('[xname=inpresponsavelPelaExecucaoDoServico]'),
    productId: document.querySelector('[xname=inpcodProduto]'),
    productType: document.querySelector('[xname=inptipoProduto]')
  }

  const state = {
    productId: $refs.productId.value,
    productType: $refs.productType.value,
    reviwer: $refs.reviwerCodUser.value
  }

  /**
   * Public Methods
   */
  function init () {
    if ($refs.productType.value === '') {
      setProductType()
    }

    if ($refs.reviwerCodUser.value === '') {
      setReviewer()
    }
  }

  /**
   * Private Methods
   */
  function setProductType () {
    const productType = getProductTypeById(state.productId)

    $refs.productType.value = productType
    state.productType = productType
  }

  function setReviewer () {
    const reviwer = getReviewerByProductType(state.productType)

    $refs.reviwerCodUser.value = reviwer
    state.reviwer = reviwer
  }

  function getReviewerByProductType (productType) {
    if (productType === 'PPRA') {
      return $refs.ppraCodUser.value
    }

    if (productType === 'PCMSO') {
      return $refs.pcmsoCodUser.value
    }

    console.error(`[Module:TaskReviewer] Unknow product type: ${productType}`)
    return null
  }

  return {
    init
  }
}

export default TaskReviewer()

/**
 * Helpers
 */
const getProductTypeById = id => {
  if (id === '2.02.01.01.03.001.001') {
    return 'PPRA'
  }

  if (id === '2.02.01.01.08.001.001') {
    return 'PCMSO'
  }

  console.error(`[Module:TaskReviewer] Unknow product id: ${id}`)
  return null
}
