import { titleCase } from 'orquestra-helpers'
import ListTrunk from '../../components/list-trunk'

const ReportContacts = function () {
  const $refs = {
    wrapper: document.querySelector('#contacts-list')
  }

  function mount (contacts) {
    const listMarkup = createList(contacts)

    $refs.wrapper
      .insertAdjacentHTML('beforeend', listMarkup)

    if (contacts.length) {
      ListTrunk.init('.list-trunk')
    }
  }

  return {
    mount
  }
}

export default ReportContacts()

/**
 * Template Helpers
 */
const createList = contacts => {
  if (!contacts.length) {
    return '<span class="form-text"><i>Nenhum contato informado</i></span>'
  }

  const items = contacts
    .map(contact => createListItem(contact))
    .join('')

  return `<ul class="list-trunk">${items}</ul>`
}

const createListItem = ({ contato, emailEmpresa }) =>
`<li>
  <span class="form-text">${titleCase(contato)}</span>
  <span class="form-text text-small -gapless">${emailEmpresa}</span>
</li>`
