const lives = {
  emptyMessage: 'No foi encontrado nenhum dado de vidas dentro do perodo pesquisado.',
  tabName: 'dados-vidas',
  tableId: 'tbl-lives',
  columns: [{
    key: 'quantityActiveLives',
    title: 'Vidas Ativas',
    headerPrefix: '<i class="fas fa-plus-circle color-green"></i> '
  }, {
    key: 'quantityInactiveLives',
    title: 'Vidas Inativas',
    headerPrefix: '<i class="fas fa-minus-circle color-red"></i> '
  }]
}

const coord = {
  emptyMessage: 'No foi encontrada nenhuma interao da Coordenao dentro do perodo pesquisado.',
  tabName: 'interacoes-coordenacao',
  tableId: 'tbl-coordination',
  columns: [{
    key: 'quantity',
    title: 'Registros'
  }]
}

const help = {
  emptyMessage: 'No foi encontrada nenhuma interao no Help+ dentro do perodo pesquisado.',
  tabName: 'interacoes-help',
  tableId: 'tbl-help-plus',
  columns: [{
    key: 'quantityDoubts',
    title: 'Dvidas'
  }, {
    key: 'quantityChanges',
    title: 'Mudanas'
  }, {
    key: 'quantitySupportSPlus',
    title: 'S+',
    headerPrefix: '<span class="th-sup">Suporte</span>'
  }, {
    key: 'quantitySupportCoord',
    title: 'Md. Coord.',
    headerPrefix: '<span class="th-sup">Suporte</span>'
  }, {
    key: 'quantitySupportVivaPlus',
    title: 'Viva+',
    headerPrefix: '<span class="th-sup">Suporte</span>'
  }]
}

/**
 * Public Methods
 */
function get (productType) {
  let helpConfig = help

  if (productType === 'PPRA') {
    helpConfig = removeColumnFromConfig({
      key: 'quantitySupportCoord',
      config: help
    })
  }

  return {
    lives,
    coord,
    help: helpConfig
  }
}

/**
 * Private Methods
 */
const removeColumnFromConfig = ({ key, config }) => {
  const columnIndex = config.columns
    .findIndex(col => col.key === key)

  if (columnIndex >= 0) {
    config.columns.splice(columnIndex, 1)
  }

  return config
}

export default {
  get
}
