import ComponentError from '../helpers/component-error'
import resolveReference from '../helpers/component-reference'

const defaults = {
  baseHeight: 30, // minimum textarea height in pixels (30px, inherited from Bootstrap defaults text input height)
  textareaClass: 'textarea-autosize'
}

export default function TextareaAutosize (reference, params) {
  params = {
    ...defaults,
    ...params,
    name: 'TextareaAutosize'
  }

  const elements = resolveReference(reference, params.name)

  if (elements instanceof HTMLElement) {
    const [element] = elements
    return createInstance(element, params)
  }

  return elements
    .map(element => createInstance(element, params))
    .filter(element => element !== undefined)
}

function createInstance (element, params) {
  const { textarea } = getOrquestraField(element, params)

  if (!textarea) {
    return false
  }

  const instance = {
    element,
    textarea,
    update,
    destroy
  }

  addListners(textarea)
  mount()

  textarea._textareaAutosize = instance

  return instance

  /**
   *  Private Methods
   */
  function addListners (textarea) {
    textarea.addEventListener('input', setHeight)
    textarea.addEventListener('focus', setHeight)
  }

  function setHeight () {
    const height = getHeight(this)

    // always apply the style to the instance textarea
    // avoid style cloned textarea created by `setInitialHeight`
    instance.textarea
      .style.height = `${height}px`
  }

  function setInitialHeight () {
    const clone = instance.textarea.cloneNode(true)

    clone.style.cssText = `
      position: fixed;
      top: 0;
      left: 0;
      box-sizing: border-box;
      height: ${params.baseHeight}px;
      overflow: hidden;
      resize: none;
      visibility: hidden;
      pointer-events: none;
    `
    document.body.append(clone)
    setHeight.apply(clone)
    clone.remove()
  }

  function getHeight (textarea) {
    textarea.style.height = 0

    const lines = (textarea.scrollHeight + 2) / params.baseHeight
    const height = lines * params.baseHeight

    return height
  }

  function mount () {
    instance.textarea
      .classList.add(params.textareaClass)

    setInitialHeight()
  }

  /**
   *  Public Methods
   */
  function update () {
    setHeight.apply(instance.textarea)
  }

  function destroy () {
    instance.textarea
      .classList.remove('textarea-autosize')

    instance.textarea
      .removeEventListener('input', setHeight)

    instance.textarea
      .removeEventListener('focus', setHeight)
  }
}

/**
 *  Helper Functions
 */
function getOrquestraField (element, params) {
  const textarea = element.nodeName.toLowerCase() === 'textarea'
    ? element
    : element.querySelector('textarea[xname]')

  const textvalue = element.querySelector('div[xid]')
  const inputHidden = element.querySelector('input[type=hidden][xname]')

  if (!textarea && !textvalue && !inputHidden) {
    throw new ComponentError({
      name: params.name,
      message:
      'Erro ao criar a instncia do componente.\nNenhum campo do Orquestra encontrado para a referncia informada',
      type: 'instace_field_not_found'
    })
  }

  return {
    textarea,
    textvalue
  }
}
