function mount (btnRadioWrapper, hidePrefix) {
  const select = btnRadioWrapper.querySelector('select')
  const hasBtnGroupMarkup = btnRadioWrapper.querySelector('.btn-group')

  if (!hasBtnGroupMarkup) {
    const options = getOptions(select, hidePrefix)
    const btnGroup = createButtonGroup(options)

    select.insertAdjacentHTML('afterend', btnGroup)
  } else {
    reset(btnRadioWrapper)
  }

  handleChange.apply(select)
  select.addEventListener('change', handleChange)

  btnRadioWrapper
    .querySelectorAll('.btn-radio')
    .forEach(btn => setTriggers(btn))
}

function setTriggers (btn) {
  btn.addEventListener('click', handleClick)
}

function handleClick () {
  const optionValue = this.dataset.option
  const btnRadioWrapper = this.closest('.btn-radio-wrapper')
  const select = btnRadioWrapper.querySelector('select')

  reset(btnRadioWrapper)
  this.classList.add('-active')
  select.value = optionValue
}

function handleChange () {
  const btnRadioWrapper = this.closest('.btn-radio-wrapper')

  if (this.value === '') {
    return reset(btnRadioWrapper)
  }

  const btnRadio = btnRadioWrapper
    .querySelector(`.btn-radio[data-option='${this.value}']`)

  handleClick.apply(btnRadio)
}

function resetState (btn) {
  btn.classList.remove('-active')
}

/**
 * Helpers
 */
const getOptions = (select, hidePrefix) =>
  [...select.options]
    .filter(option => option.value)
    .map(option => {
      const value = option.value
      const label = hidePrefix
        ? value.substring(2)
        : value

      return { value, label }
    })

const createButtonGroup = options => {
  const buttons = options.map(option => createButton(option))
  return `<div class="btn-group">${buttons.join('')}</div>`
}

const createButton = ({ value, label }) =>
  `<button type="button" class="btn btn-radio" data-option="${value}">${label}</button>`

/**
 * Public API
 */
function init (btnRadioWrapperSelector, hidePrefix = false, context = document) {
  context
    .querySelectorAll(btnRadioWrapperSelector)
    .forEach(btnRadioWrapper => mount(btnRadioWrapper, hidePrefix))
}

function reset (btnRadioWrapper) {
  btnRadioWrapper
    .querySelectorAll('.btn-radio')
    .forEach(btn => resetState(btn))
}

function destroy (btnRadioWrapper) {
  btnRadioWrapper
    .querySelectorAll('.btn-radio')
    .forEach(btn => {
      btn.removeEventListener('click', handleClick)
      resetState(btn)
    })
}

export default {
  init,
  reset,
  destroy
}
