function mount (radio, radioElementSelector) {
  const container = radio.closest(radioElementSelector)
  const radioValue = radio.querySelector('input[type=radio]').value
  const marker = getRadioMarker()
  const radioHelper = container.querySelector(`.radio-card-helper[data-radio-option="${radioValue}"]`)

  radio.insertAdjacentHTML('beforeend', marker)

  if (radioHelper) {
    addRadioHelper(radioHelper, radio)
  }
}

function addRadioHelper (helper, radio) {
  const clonedHelper = helper.cloneNode(true)

  delete clonedHelper.dataset.radioOption

  radio.insertAdjacentElement('beforeend', clonedHelper)
}

const getRadioMarker = () =>
  '<div class="radio-marker"></div>'

/**
 * Public API
 */
function init (radioElementSelector, context = document) {
  context
    .querySelectorAll(`${radioElementSelector} .radio`)
    .forEach(radio => mount(radio, radioElementSelector))
}

export default {
  init
}
