function mount (tabs) {
  const tabPanes = tabs.nextElementSibling
  const hasPanes = tabPanes.classList.contains('tab-panes')

  if (!hasPanes) {
    return console.error('[Tabs Component] .tab-panes not found, must be used next to .tabs element')
  }

  tabs
    .querySelectorAll('.tab')
    .forEach(tab => setTriggers(tab))
}

function setTriggers (tab) {
  tab.addEventListener('click', handleClick)
}

function handleClick (e) {
  e.preventDefault()

  const tabDataRef = this.dataset.tab
  const isActive = this.classList.contains('-active')
  const isDisabled = this.classList.contains('-disabled')
  const tabPane = this
    .closest('.tabs')
    .nextElementSibling
    .querySelector(`.tab-pane[data-tab="${tabDataRef}"]`)

  if (!tabPane) {
    return console.error(`[Tabs Component] element not found for data-tab ${tabDataRef}`)
  }

  if (isActive || isDisabled) {
    return
  }

  showTab(this)
}

/**
 * Helpers
 */
const getTabPane = tab =>
  tab
    .closest('.tabs')
    .nextElementSibling
    .querySelector(`.tab-pane[data-tab="${tab.dataset.tab}"]`)

/**
 * Public API
 */
function init (tabsElementSelector, context = document) {
  context
    .querySelectorAll(tabsElementSelector)
    .forEach(tabs => mount(tabs))
}

function reset (tabs) {
  tabs
    .querySelectorAll('.tab')
    .forEach(tab => hideTab(tab))
}

function destroy (tabs) {
  tabs
    .querySelectorAll('.tab')
    .forEach(tab => {
      tab.removeEventListener('click', handleClick)
      hideTab(tab)
      disabledTab(tab)
    })
}

function showTab (tab) {
  const tabPanel = getTabPane(tab)
  const tabs = tab.closest('.tabs')

  reset(tabs)

  tab.classList.add('-active')
  tab.classList.remove('-pulse')
  tabPanel.classList.add('-active')
}

function hideTab (tab) {
  const tabPane = getTabPane(tab)

  tab.classList.remove('-active')
  tabPane.classList.remove('-active')
}

function enableTab (tab) {
  tab.classList.remove('-disabled')
}

function pulseTab (tab) {
  if (!tab.classList.contains('-active')) {
    tab.classList.add('-pulse')
  }
}

function disabledTab (tab) {
  const tabPane = getTabPane(tab)

  tab.classList.add('-disabled')
  tab.classList.remove('-active')
  tabPane.classList.remove('-active')
}

export default {
  init,
  reset,
  destroy,
  showTab,
  hideTab,
  enableTab,
  pulseTab,
  disabledTab,
  /**
   * `.tab` aliases
   */
  tab: {
    show: showTab,
    hide: hideTab,
    enable: enableTab,
    pulse: pulseTab,
    disabled: disabledTab
  }
}
