const ExamsControls = function () {
  const alertId = 'search-data-alert'

  const $refs = {
    actionsWrapper: document.querySelector('#controllers #buttons'),
    btnFinish: document.querySelector('#btnFinish'),
    actionsWell: document.querySelector('#controllers .well'),
    alert: null
  }

  const state = {
    emitter: null
  }

  /**
   * Public Methods
   */
  function mount ({ emitter }) {
    const alertMarkup = createAlert(alertId)

    state.emitter = emitter
    state.emitter.on('search-done', enable)

    $refs.actionsWrapper
      .insertAdjacentHTML('beforebegin', alertMarkup)

    $refs.alert = document.querySelector(`#${alertId}`)

    disable()
  }

  /**
   * Private Methods
   */
  function enable () {
    $refs.btnFinish.disabled = false

    $refs.actionsWell
      .classList.remove('hidden')

    $refs.alert
      .classList.add('hidden')
  }

  function disable () {
    $refs.btnFinish.disabled = true

    $refs.actionsWell
      .classList.add('hidden')

    $refs.alert
      .classList.remove('hidden')
  }

  return {
    mount
  }
}

export default ExamsControls()

/**
 * Helpers
 */
const createAlert = id =>
`<div class="alert alert-info" id="${id}">
  <strong>Ateno!</strong> Antes de concluir a atividade voc deve <strong>pesquisar</strong> por um perodo de levantamento.
</div>`
