import { titleCase, isEmail } from 'orquestra-helpers'
import Chips from '../../components/chips'

const ReportSendTo = function () {
  const $refs = {
    wrapper: document.querySelector('#send-to-group'),
    placeholder: document.querySelector('[xname=inpenviarRelatorioPara]'),
    input: document.querySelector('[xname=inplistaEmailsRelatorio]')
  }

  /**
   * Public Methods
   */
  function mount (contacts, readonly) {
    const contactsParsed = resolveContacts(contacts)
    const chipsConfig = {
      readonly,
      editable: !readonly
    }

    Chips.init('.chips', {
      ...chipsConfig,
      data: contactsParsed,
      inputPlaceholder: 'Adicionar E-mail',
      target: $refs.placeholder,
      errorMessage: 'Formato de e-mail invlido',
      validation: validateEmailFormat
    })

    $refs.placeholder
      .addEventListener('change', updateInputEmails)

    updateInputEmails
      .call($refs.placeholder)
  }

  function show () {
    $refs.wrapper
      .classList.remove('hidden')
  }

  function hide () {
    $refs.wrapper
      .classList.add('hidden')
  }

  /**
   * Private Methods
   */
  function resolveContacts (contacts) {
    if ($refs.placeholder.value !== '') {
      return $refs.placeholder.value.split(';')
    }

    if (!contacts.length) {
      return contacts
    }

    return contacts.map(formatContactToChipEmail)
  }

  function updateInputEmails () {
    $refs.input.value = filterEmails(this.value).join(';')
  }

  return {
    mount,
    show,
    hide
  }
}

export default ReportSendTo()

/**
 * Helpers
 */
const formatContactToChipEmail = ({ contato, emailEmpresa }) =>
  `${titleCase(contato)} (${emailEmpresa})`

const filterEmails = contactsPlaceholder =>
  contactsPlaceholder
    .split(';')
    .map(getEmailFromContact)

const validateEmailFormat = contact => {
  const email = getEmailFromContact(contact)

  // Must be formated as: Name (email@example.com) when used with parentheses
  if (
    contact.split(')').length > 2 ||
    contact.split('(').length > 2 ||
    (contact.includes('(') && contact.slice(-1) !== ')')
  ) {
    return false
  }

  return isEmail(email)
}

const getEmailFromContact = contact => {
  const data = contact.replace(')', '').split('(')
  const email = data.length > 1
    ? data[1] // Name (email@example.com)
    : data[0] // email@example.com

  return email
}
