import ReportContacts from './report-contacts'
import ReportSendTo from './report-send-to'
import ReportType from './report-type'

const Reports = function () {
  const state = {
    contacts: [],
    alias: null,
    emitter: null
  }

  const $refs = {
    section: document.querySelector('#section-send-options')
  }

  /**
   * Public Methods
   */
  function init ({ alias, emitter }) {
    const isSendToReadonly = ['formatacaoAnual'].includes(alias)
    state.contacts = getContactsData([
      'contato',
      'emailEmpresa'
    ])

    state.alias = alias
    state.emitter = emitter

    state.emitter.on('search-done', mount)
    state.emitter.on('send-report-change', onSendReportChange)


    ReportSendTo.mount(state.contacts, isSendToReadonly)
    ReportContacts.mount(state.contacts)

    if (isSendToReadonly) {
      mount()
    }
  }

  /**
   * Private Methods
   */
  function mount () {
    const showSendTo = ['elaboracaoAnual', 'complementacaoAnual', 'formatacaoAnual'].includes(state.alias)
    const mountReportType = ['elaboracao', 'complementacao'].includes(state.alias)

    $refs.section
      .classList
      .remove('hidden')

    if (showSendTo) {
      ReportSendTo.show()
    }

    if (mountReportType) {
      ReportType.mount({ emitter: state.emitter })
    }
  }

  function onSendReportChange (isEnable) {
    isEnable
      ? ReportSendTo.show()
      : ReportSendTo.hide()
  }

  return {
    init
  }
}

export default Reports()

/**
 * Helpers
 */
const getContactsData = fields => {
  const contactsListSize = Math.max(
    ...fields.map(field =>
      document.querySelectorAll(`[xname=inp${field}]`).length
    )
  )

  const contactsData = Array(contactsListSize)
    .fill(null)
    .map((none, index) =>
      fields.reduce((row, field) => {
        const value = document.querySelectorAll(`[xname=inp${field}]`)[index].value

        if (value) {
          row[field] = value
        }

        return row
      }, {})
    )
    .filter(contact => Object.keys(contact).length)

  return contactsData
}
